import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ModalController, Platform } from "@ionic/angular";
import { jqxGridComponent } from "jqwidgets-ng/jqxgrid";
import { AjaxService } from "src/app/services/ajax.service";
import { CommonService } from "src/app/services/common.service";
import { serverUrl } from "src/environments/environment";

@Component({
  selector: "app-esim-print-popup",
  templateUrl: "./esim-print-popup.component.html",
  styleUrls: ["./esim-print-popup.component.scss"],
})
export class EsimPrintPopupComponent implements OnInit {
  @ViewChild("myGrid", { static: false }) myGrid: jqxGridComponent;
  renderer: (row: number, column: any, value: string) => string;
  page: any[];
  source: { localdata: any };
  dataAdapter: any;
  tableData: any;
  columns: any;

  @Input() value;
  myPlatform: any;
  selectedRow: any;

  Apmlogo =
    "data:image/png;base64,";
  imeivalue: any = [];
  serialNumbers: any = [];
  popvalue: any;
  printvalue = [];
  jjj: any[];
  dd: any;
  addressvalue: any = [];
  splitvalue = "";
  visible: boolean = false;

  constructor(
    private platform: Platform,
    private formBuilder: FormBuilder,
    private modalController: ModalController,
    private ajaxService: AjaxService,
    private commonService: CommonService
  ) {}

  cancelBtn() {
    this.modalController.dismiss();
  }

  getDatas() {
    this.commonService.presentLoader();
    const url =
      serverUrl.web +
      "/esim/getBoxStickerPrint?invoiceno=" +
      this.value.invoiceno;
    this.ajaxService.ajaxGet(url).subscribe((res) => {
      this.commonService.dismissLoader();
      this.tableData = res;
      this.renderer = (row: number, column: any, value: string) => {
        if (value == "" || null || undefined || value == ",") {
          return (
            '<span style="line-height:32px;font-size:11px;color:darkblue;margin:auto;">' +
            "0" +
            "</span>"
          );
        } else {
          return (
            '<span style="line-height:32px;font-size:11px;color:darkblue;margin:auto;">' +
            value +
            "</span>"
          );
        }
      };

      this.source = { localdata: this.tableData };
      this.dataAdapter = new jqx.dataAdapter(this.source);
      this.columns = [
        {
          text: "S.No",
          datafield: "sno",
          cellsrenderer: this.renderer,
          cellsalign: "center",
          align: "center",
          width: 150,
        },

        {
          text: "New Box Number",
          datafield: "serialno",
          cellsrenderer: this.renderer,
          cellsalign: "center",
          align: "center",
          width: 150,
        },
        {
          text: "Old Box Number",
          datafield: "boxno",
          cellsrenderer: this.renderer,
          cellsalign: "center",
          align: "center",
          width: 150,
        },
        {
          text: "Quantity",
          datafield: "boxquantity",
          cellsrenderer: this.renderer,
          cellsalign: "center",
          align: "center",
          width: 150,
        },
        {
          text: "Print",
          datafield: "Print",
          columntype: "button",
          cellsalign: "center",
          align: "center",
          width: 80,
          cellsrenderer: (): string => {
            return "Print";
          },
          buttonclick: (row): void => {
            this.getimei();
          },
        },
      ];
    });
  }

  myGridOnRowSelect(event: any): void {
    this.selectedRow = event.args.row.bounddata;
  }

  getaddress() {
    const url = serverUrl.web + "/esim/getStickerHeaderDetails";
    this.ajaxService.ajaxGet(url).subscribe((res) => {
      this.commonService.dismissLoader();
      this.addressvalue = res;
    });
  }

  getimei() {
    this.commonService.presentLoader();
    const url =
      serverUrl.web +
      "/esim/getEsimProductionDetail?companyid=apm&serialno=" +
      this.selectedRow.serialno;
    this.ajaxService.ajaxGet(url).subscribe((res) => {
      this.commonService.dismissLoader();
      this.imeivalue = res;
      this.printvalue = this.selectedRow;
      this.splitvalue = this.selectedRow.boxno.slice(13);
      this.sno();
      this.getaddress();
      setTimeout(() => {
        this.printModel();
      }, 1000);
    });
  }

  sno() {
    let serialNumbers = [];
    this.dd = this.imeivalue.length;
    for (let i = 1; i <= this.imeivalue.length; i++) {
      serialNumbers.push(i);
    }
    this.serialNumbers = serialNumbers;
  }

  printModel() {
    var printWindow = window.open("height=700,width=800");
    printWindow.document.write("<html>");

    var divContents = document.getElementById("pdf").innerHTML;
    printWindow.document.write(divContents);
    printWindow.document.write("</body></html>");
    printWindow.print();
    printWindow.document.close();
  }

  ngAfterViewInit() {
    this.myGrid.pagesizeoptions(["100", "200", "500", "1000"]);
  }

  ngOnInit() {
    this.getDatas();
    this.popvalue = this.value;
  }
}
